.btn {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  padding: 17px 0;
  font-weight: 600;
  max-width: 130px;
  width: 100%;
  font-size: 14px;
  text-transform: uppercase;
  transition: background-color 0.3s ease, color 0.3s ease;

  &--fill {
    background-color: var(--accent-color);
    color: var(--light-color);
  }

  &--light {
    padding: 16px 0;
    font-weight: 400;
    background-color: var(--light-color);
    color: var(--black-color);
    border: 1px solid var(--dove-gray-color);
    text-transform: initial;
  }

  &--shop {
    &.added {
      color: var(--alizarin-crimson-color);
      animation: btn-add 0.3s ease-in-out;
    }
  }

  &--favorites {
    &.added-favorites {
      animation: btn-add 0.3s ease-in-out;

      svg {
        fill: var(--alizarin-crimson-color) !important;
      }
    }
  }

  @keyframes btn-add {
    from {
      transform: scale(1);
    }

    50% {
      transform: scale(1.2);
    }

    to {
      transform: scale(1);
    }
  }

  &:focus {
    outline: 2px solid var(--accent-color);
    outline-offset: 4px;
  }

  &:active {
    background-color: var(--dove-gray-color);
  }
}
